import { useTranslation } from "react-i18next";
import { useCallback, useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import { assestsNavbarUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import { useAcademicDepartmentAllClassMaster } from "../../../../Hooks/ApiHooks/AcademicDepartmentManager/academic-department-manager-api";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../../Utils/Functions/debounce";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import Loading from "../../../../Loader/Loading";
import InstituteAcademicDepartmentClsCard from "./InstituteAcademicDepartmentClsCard";
const InstituteAcademicDepartmentCls = ({ did, instituteId, openAs }) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const {
    academicDepartmentAllClassMaster,
    academicDepartmentAllClassMasterLoading,
  } = useAcademicDepartmentAllClassMaster({
    data: {
      did: did,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !did,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (academicDepartmentAllClassMaster?.list?.length) {
      let obj = nextPage(academicDepartmentAllClassMaster?.list?.length);
      setState(() => obj);
    }
  }, [academicDepartmentAllClassMaster?.list]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  return (
    <>
      <UserMemberContentWrapper>
        {openAs === "DEPARTMENT_HEAD" ? (
          <div className={style.fm_header_container}>
            <h6>{t("classes")}</h6>
            <div className={style.fm_search}>
              <section className={style.fm_search_container}>
                <div className={style.fm_search_container_inner}>
                  <img
                    className={style.fm_search_icon}
                    alt="search icon"
                    src={`${assestsNavbarUrl}/navbar-search.svg`}
                  />
                  <input
                    type="text"
                    placeholder={t("search")}
                    onChange={onSearchEvent}
                  />
                </div>
              </section>
            </div>
          </div>
        ) : (
          <div className={style.fm_search_full}>
            <section className={style.fm_search_full_container}>
              <div className={style.fm_search_full_container_inner}>
                <img
                  className={style.fm_search_full_icon}
                  alt="search icon"
                  src={`${assestsNavbarUrl}/navbar-search.svg`}
                />
                <input
                  type="text"
                  placeholder={t("search")}
                  onChange={onSearchEvent}
                />
              </div>
            </section>
          </div>
        )}
      </UserMemberContentWrapper>
      <BorderBottom />
      <UserMemberContentWrapper
        customStyle={{
          paddingTop: "0.3rem",
          paddingBottom: "1rem",
        }}
      >
        {academicDepartmentAllClassMaster?.list?.map((cls, index) =>
          academicDepartmentAllClassMaster?.list?.length === index + 1 ? (
            <div key={cls?._id} ref={ref}>
              <InstituteAcademicDepartmentClsCard
                cls={cls}
                instituteId={instituteId}
                openAs={openAs}
              />
            </div>
          ) : (
            <InstituteAcademicDepartmentClsCard
              cls={cls}
              key={cls?._id}
              instituteId={instituteId}
              openAs={openAs}
            />
          )
        )}
        {academicDepartmentAllClassMasterLoading ? (
          search ? (
            <QvipleLoader />
          ) : (
            <Loading />
          )
        ) : search ? (
          academicDepartmentAllClassMaster?.list?.length === 0 && (
            <EmptyMessage title="No class found related this search." />
          )
        ) : (
          academicDepartmentAllClassMaster?.list?.length === 0 && (
            <EmptyMessage title="No class." />
          )
        )}
      </UserMemberContentWrapper>
    </>
  );
};

export default InstituteAcademicDepartmentCls;
