import { Link, useLocation, useParams } from "react-router-dom";
import style from "../../../InstituteFeedback/InstituteFeedback.module.css";
import { assestsAcademicDepartmentUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
const InstituteAcademicDepartmentClsCard = ({ cls, instituteId, openAs }) => {
  const params = useParams();
  const getQuery = useLocation();
  return (
    <Link
      to={
        openAs === "DEPARTMENT_HEAD"
          ? `/q/${params.username}/member/staff/academic/department/${params.name}?type=cls-detail`
          : `/q/${params.username}/institute/member/academic/department/detail?type=cls-detail`
      }
      state={{
        ...getQuery.state,
        clsId: cls?._id,
        clsName: `${cls?.className} (${cls?.department?.dName ?? ""})`,
        instituteId: instituteId,
      }}
    >
      <div className={style.iadcc_card_hover}>
        <div className={style.sf_card_contatiner}>
          <img
            src={`${assestsAcademicDepartmentUrl}/cls-icon.svg`}
            loading="lazy"
            alt="avatar"
            style={{
              borderRadius: "0",
              height: "2rem",
              width: "2rem",
            }}
          />

          <div className={style.sf_card_contatiner_inner}>
            <h6>
              {cls?.className ?? ""}
              <span
                style={{
                  fontSize: "12px",
                }}
              >
                {" "}
                ({cls?.department?.dName ?? ""})
              </span>
            </h6>
          </div>
        </div>
      </div>
    </Link>
  );
};

export default InstituteAcademicDepartmentClsCard;
